import React from 'react'
import { ReactElement } from 'react'
import { ReactNode } from 'react'
import classNames from 'classnames'

function * range (size : number) : Iterable<number> {
  for (let index = 0; index < size; ++index) {
    yield index
  }
}

export function Spell (props : any) : ReactElement {
  const runesByRow : number = Math.ceil(props.runes.length / props.rows)

  return (
    <>
      <div className={classNames('spell d-none d-md-block', props.className)}>
        {
          [...range(props.rows)].map(function (index : number) : ReactElement[] {
            const runes : ReactElement[] = []

            if (index > 0) {
              runes.push(
                <div className='w-100' key={props.runes.length + index} />
              )
            }

            for (let rune = 0; rune < runesByRow; ++rune) {
              runes.push(
                <img className='spell-rune' key={rune * runesByRow + index} src={'./images/runic-' + props.runes[rune * runesByRow + index] + '.svg'} />
              )
            }

            return runes
          }).reduce(function (a : ReactElement[], b : ReactElement[]) : ReactElement[] {
            return a.concat(b)
          })
        }
      </div>

      <div className={classNames('spell d-block d-md-none', props.className)}>
        {
          props.runes.map(function (rune : string, index : number) : ReactElement {
            return <img className='spell-rune' key={index} src={'./images/runic-' + rune + '.svg'} />
          })
        }
      </div>
    </>
  )
}
